import React, { Component } from 'react';
import SectionAboutMe from '../../components/Sections/SectionAboutMe.js';
import $ from "jquery";
import { Waypoint } from 'react-waypoint';

// Section container
// This is the template for an individual section within.
class Section extends Component {
  render() {
    // Based on title, display a different set of content.
    this.content = this.renderSwitch(this.title);

    return (
      <Waypoint
        onEnter={this.handleWaypointEnter}
        title={this.props.title}
        passTitleUpward={this.props.passTitleUpward}>
          <div className="section">
            <div className="spacer" style={{marginTop: '100vh', height: '3000px'}}></div>
            {/* SECTION CONTENT Component */}
          </div>
      </Waypoint>
    );
  }

  handleWaypointEnter() {
    this.props.passTitleUpward(this.props.title);
  }

  renderSwitch(title) {
    switch (title) {
      case 'aboutme':
        return <SectionAboutMe />;
      case 'skills':
        return <SectionAboutMe />;
      case 'experience':
        return <SectionAboutMe />;
      case 'projects':
        return <SectionAboutMe />;
      case 'contact':
        return <SectionAboutMe />;
    }
  }

}

export default Section;