import React from 'react';
import $ from "jquery";

// Full.js serves as the container for the entire app.

// STYLESHEET
import '../../styles/compiler.scss';

// CONTAINERS
import Splash from '../Splash/Splash.js';
import Section from '../Section/Section.js';

// COMPONENTS

// IMAGES

//import logo from './logo.svg';

class Full extends React.Component {
  constructor(props) {
    super(props);

    this.aboutme = "aboutme";
    this.skills = "skills";
    this.experience = "experience";
    this.projects = "projects";
    this.contact = "contact";

    this.sidebarNav = $('.splash-left-sidebar-nav li');

    this.getTitleFromWaypoint = this.getTitleFromWaypoint.bind(this);
  }

  componentDidMount() {
    this.sidebarNav = $('.splash-left-sidebar-nav li');
  }

  getTitleFromWaypoint(waypointTitle) {
    this.setState({
      currentSelectedTitle: waypointTitle
    });
    this.updateSidebarNav();
  }  
  
  render() {
    return (
      <div className="full">
        <Splash />
        <Section title={this.aboutme} passTitleUpward={this.getTitleFromWaypoint} />
        <Section title={this.skills}  passTitleUpward={this.getTitleFromWaypoint} />
        <Section title={this.experience} passTitleUpward={this.getTitleFromWaypoint}  />
        <Section title={this.projects} passTitleUpward={this.getTitleFromWaypoint}  />
        <Section title={this.contact} passTitleUpward={this.getTitleFromWaypoint}  />
      </div>
    );
  }

  updateSidebarNav() {
    // Remove active class from all of the sidebar nav anchors.
    this.sidebarNav.find('a').removeClass('active');

    // Add active class to just this one.
    this.sidebarNav.filter('.splash-nav-' + this.state.currentSelectedTitle).find('a').addClass('active'); 
  }
  
}

export default Full;
